/* Vars */

/* Typography */
$font-default: Raleway, Arial, sans-serif;
$ff: $font-default;
$font-size: 14px;
$fz: $font-size;
$line-height: 1.3;
$lh: $line-height;
$letter-spacing: normal;
$ls: $letter-spacing;

/* Layout */
$wrapper-size: 1140px;

$break-lg: 1200px;
$break-md: 992px;
$break-sm: 768px;
$break-xs: 576px;

/* Colors */
$primary-color: green;
$second-color: #3d176b;
$third-color: #90c004;
$four-color: #dd892d;
$background-color: #fff;
$text-color: #282b30;
